import Component from "vue-class-component";
import mixin from "../../config/mixin";
import { Action } from "vuex-class";
import { Toast } from "vant";
import axios from "axios";

@Component({
  components: {},
  mixins: [mixin]
})
export default class shopIndex extends Vue {
  value = "";
  onCancel() {
    this.$router.go(-1);
  }
  loading = false;
  goProductDetail(goodsId) {
    this.$router.push({
      path: "/productdetail",
      query: {
        goodsId: goodsId
      }
    });
  }
  loadMore() {
    console.log("刷新");
    this.loading = true;
    let self = this;
    setTimeout(() => {
      if (!self.loading) {
        self.pageindex = self.pageindex + 10;
        self.onSearch();
        self.loading = false;
      }
    }, 1000);
  }

  doChange() {
    this.catModel = false;
    this.catId = "";
    this.sortName = "";
    this.sortStatus = "";
    this.model = false;
  }
  doCatFilter(row) {
    this.catModel = false;
    this.catId = row.catId;
    this.onSearch();
  }
  catModel = false;
  catId = "";
  doPriceFitler() {
    this.sortName = "PRICE";
    switch (this.sortStatus) {
      case "":
        this.sortStatus = true;
        break;
      case true:
        this.sortStatus = false;
        break;
      case false:
        this.sortStatus = "";
        break;
    }

    this.onSearch();
  }

  sortName = "";
  sortStatus: any = "";
  doSelect(n) {
    this.value = n;
    this.onSearch();
  }
  pageindex = 10;
  queryList: any = [];
  doClear() {
    this.queryList = [];
    localStorage.queryList = JSON.stringify(this.queryList);
  }
  removeByValue(arr, val) {
    for (var i = 0; i < arr.length; i++) {
      if (arr[i] == val) {
        arr.splice(i, 1);
        break;
      }
    }
  }

  onSearch() {
    if ((this.value || "") == "") {
      return;
    }
    let data = {
      userId: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
        .userId,
      token: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
        .token,
      keyWord: this.value,
      catId: this.catId,
      page: 0,
      pageSize: this.pageindex
    };
    if (this.sortStatus != "") {
      (<any>Object).assign(data, {
        sortName: this.sortName,
        sortStatus: this.sortStatus
      });
    }
    Vue.prototype.$reqFormPost("/user/goods/list", data, res => {
      if (res == null) {
        console.log("网络请求错误！");
        return;
      }
      if (res.data.status != 200) {
        console.log(
          "需控制错误码" + res.data.status + ",错误信息：" + res.data.message
        );
        Toast(res.data.message);
        return;
      }

      this.queryList = localStorage.queryList
        ? JSON.parse(localStorage.queryList)
        : [];

      if (
        this.queryList.filter(item => {
          return item === this.value;
        }).length > 0
      ) {
        this.removeByValue(this.queryList, this.value);
      }
      this.queryList.unshift(this.value);
      if (this.queryList.length > 3) {
        this.queryList = [
          this.queryList[0],
          this.queryList[1],
          this.queryList[2]
        ];
      }
      localStorage.queryList = JSON.stringify(this.queryList);

      if (res.data.data.goodsList.length == 20) {
        this.loading = false;
      }
      this.goodsList = res.data.data.goodsList;

      this.model = true;
    });
  }
  model = false;
  goodsList = [];
  hotwordList = [];

  doFilter() {
    Vue.prototype.$reqUrlGet("/hotword/query", {}, res => {
      if (res == null) {
        console.log("网络请求错误！");
        return;
      }
      if (res.data.status != 200) {
        console.log(
          "需控制错误码" + res.data.status + ",错误信息：" + res.data.message
        );
        Toast(res.data.message);
        return;
      }

      this.hotwordList = res.data.data;
    });
  }
  slectQuery(item) {
    this.value = item;
  }
  gethotword() {
    Vue.prototype.$reqUrlGet("/hotword/query", {}, res => {
      if (res == null) {
        console.log("网络请求错误！");
        return;
      }
      if (res.data.status != 200) {
        console.log(
          "需控制错误码" + res.data.status + ",错误信息：" + res.data.message
        );
        Toast(res.data.message);
        return;
      }

      this.hotwordList = res.data.data;
    });
  }
  handlePX(CssName, PxNumber) {
    return (
      CssName +
      ":" +
      this.$store.getters[Vue.prototype.MutationTreeType.SYSTEM].availWidth /
        750 *
        PxNumber +
      "px;"
    );
  }

  getCatList() {
    Vue.prototype.$reqFormPost("/user/cat/querytree", {}, res => {
      if (res == null) {
        console.log("网络请求错误！");
        return;
      }
      if (res.data.status != 200) {
        console.log(
          "需控制错误码" + res.data.status + ",错误信息：" + res.data.message
        );
        Toast(res.data.message);
        return;
      }
      console.log("selectCategoyItem", res.data.data.children);
      this.catList = res.data.data.children;
    });
  }
  catList = [];
  mounted() {
    this.queryList = localStorage.queryList
      ? JSON.parse(localStorage.queryList)
      : [];

    this.sortStatus == "";
    this.gethotword();
    this.getCatList();
(<any>document.getElementsByClassName('van-field__control')[0]).style.marginLeft = "20px"
  }
}



